import React, { useState, useEffect, useRef } from 'react'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import assets from '../utils/assets'

gsap.registerPlugin(ScrollTrigger)

export default function Gallery(){
  const [open, setOpen] = useState(null)
  const sectionRef = useRef(null)
  const titleRef = useRef(null)
  const imagesRef = useRef([])

  useEffect(() => {
    const ctx = gsap.context(() => {
      // Animação do título
      if(titleRef.current) {
        gsap.fromTo(titleRef.current,
          { opacity: 0, y: 50, filter: 'blur(10px)' },
          {
            opacity: 1,
            y: 0,
            filter: 'blur(0px)',
            duration: 1,
            ease: 'power3.out',
            scrollTrigger: {
              trigger: titleRef.current,
              start: 'top 85%',
              toggleActions: 'play none none reverse'
            }
          }
        )
      }

      // Animação stagger das imagens
      imagesRef.current.forEach((img, idx) => {
        if(img) {
          gsap.fromTo(img,
            { opacity: 0, scale: 0.8, filter: 'blur(10px)' },
            {
              opacity: 1,
              scale: 1,
              filter: 'blur(0px)',
              duration: 0.8,
              ease: 'power2.out',
              delay: idx * 0.1,
              scrollTrigger: {
                trigger: img,
                start: 'top 90%',
                toggleActions: 'play none none reverse'
              }
            }
          )
        }
      })
    }, sectionRef)

    return () => ctx.revert()
  }, [])

  useEffect(() => {
    function onKey(e){
      if(open === null) return
      if(e.key === 'Escape') setOpen(null)
      if(e.key === 'ArrowRight') setOpen((s) => (s === null ? 0 : Math.min(assets.photos.length-1, s+1)))
      if(e.key === 'ArrowLeft') setOpen((s) => (s === null ? 0 : Math.max(0, s-1)))
    }
    window.addEventListener('keydown', onKey)
    return () => window.removeEventListener('keydown', onKey)
  }, [open])

  useEffect(() => {
    if(open !== null) {
      // Animação de entrada do lightbox
      const lightbox = document.querySelector('.lightbox-content')
      if(lightbox) {
        gsap.fromTo(lightbox,
          { opacity: 0, scale: 0.8, filter: 'blur(20px)' },
          { opacity: 1, scale: 1, filter: 'blur(0px)', duration: 0.6, ease: 'power3.out' }
        )
      }
    }
  }, [open])

  return (
    <section 
      id="gallery" 
      ref={sectionRef}
      className="py-24 md:py-32 bg-black grain-bg relative overflow-hidden"
    >
      <div className="container">
        <h2 
          ref={titleRef}
          className="text-4xl md:text-6xl lg:text-7xl font-heading text-gold mb-4 reveal"
        >
          Galeria
        </h2>
        <p className="text-white/60 text-lg mb-12 max-w-2xl">
          Momentos que marcaram a trajetória do grupo.
        </p>

        <div className="mt-12 columns-2 md:columns-3 lg:columns-4 gap-4 md:gap-6">
          {assets.photos.map((p, i) => (
            <div
              key={i}
              ref={el => imagesRef.current[i] = el}
              className="mb-4 md:mb-6 reveal group cursor-pointer relative overflow-hidden rounded-lg"
              onClick={() => setOpen(i)}
            >
              <img 
                src={p} 
                alt={`Foto ${i+1}`} 
                className="w-full h-auto object-cover transition-transform duration-500 group-hover:scale-110" 
                loading="lazy"
              />
              <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
              <div className="absolute inset-0 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity duration-300">
                <div className="w-16 h-16 rounded-full bg-gold/20 backdrop-blur-sm border-2 border-gold/50 flex items-center justify-center">
                  <svg className="w-8 h-8 text-gold" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0zM10 7v6m3-3H7" />
                  </svg>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Lightbox Premium */}
      {open !== null && (
        <div 
          className="fixed inset-0 z-50 flex items-center justify-center lightbox-backdrop"
          onClick={(e) => {
            if(e.target.classList.contains('lightbox-backdrop')) setOpen(null)
          }}
        >
          <div className="absolute inset-0 modal-backdrop" onClick={() => setOpen(null)}></div>
          
          <div className="lightbox-content relative z-10 max-w-[95vw] max-h-[95vh] flex items-center justify-center">
            <img 
              src={assets.photos[open]} 
              alt={`Foto ${open+1}`} 
              className="max-h-[90vh] max-w-[90vw] rounded-lg shadow-2xl object-contain"
            />
          </div>

          {/* Botões de navegação */}
          {assets.photos.length > 1 && (
            <>
              <button 
                aria-label="Anterior" 
                className="absolute left-6 z-20 w-12 h-12 rounded-full glass-strong border border-gold/30 text-gold hover:bg-gold/10 transition-all flex items-center justify-center"
                onClick={() => setOpen(i => Math.max(0, i-1))}
              >
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
              </button>
              
              <button 
                aria-label="Próxima" 
                className="absolute right-6 z-20 w-12 h-12 rounded-full glass-strong border border-gold/30 text-gold hover:bg-gold/10 transition-all flex items-center justify-center"
                onClick={() => setOpen(i => Math.min(assets.photos.length-1, i+1))}
              >
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                </svg>
              </button>
            </>
          )}

          {/* Botão fechar */}
          <button 
            aria-label="Fechar" 
            className="absolute top-6 right-6 z-20 w-12 h-12 rounded-full glass-strong border border-gold/30 text-gold hover:bg-red/20 hover:border-red transition-all flex items-center justify-center"
            onClick={() => setOpen(null)}
          >
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>

          {/* Indicador de posição */}
          <div className="absolute bottom-6 left-1/2 transform -translate-x-1/2 z-20 glass-strong px-4 py-2 rounded-full border border-gold/30">
            <span className="text-gold text-sm">
              {open + 1} / {assets.photos.length}
            </span>
          </div>
        </div>
      )}
    </section>
  )
}
